import { defHttp } from '@/utils/http/axios';

enum Api {
  //查询缺陷内容
  addFaultDangerData = '/po/fault-manage/create',
  getFaultDangerData = '/po/fault-manage/get',
  //查询期数
  getStationPeriodData = '/baseset/common/list-stationPeriod',
  //查询有缺陷的期数
  getFaultPeriodData = '/po/technical-supervision/dtl/getPeriodList?technicalSupervisionDtlId=',
  //发现人
  getStationPerson = '/baseset/common/list-stationPerson?stationId=',
  //设备名称
  getEquipmentPage = '/po/equipment/page',
}

export const getEquipmentUrl = Api.getEquipmentPage;
//查询缺陷内容
export function getFaultDangerData(params) {
  return defHttp.get({ url: Api.getFaultDangerData, params });
}

// 新增缺陷内容
export function addFaultDangerData(data) {
  return defHttp.post({ url: Api.addFaultDangerData, data });
}
//查询期数
export function getStationPeriodData(data) {
  return defHttp.post({ url: Api.getStationPeriodData, data });
}
//查询有缺陷的期数
export function getFaultPeriodData(data) {
  return defHttp.get({ url: Api.getFaultPeriodData + data.technicalSupervisionId });
}
//发现人
export function getStationPerson(data) {
  return defHttp.post({ url: Api.getStationPerson + data.stationId });
}
