<template>
    <PageWrapper dense contentFullHeight fixedHeight  contentClass="flex" style="position: relative">
  <div class="load">
	<a-spin :spinning="formState.spinning" tip="Loading...">
  <div class="process-box">
    <div class="header-box">
      <BillTitle :options="billTitleOptions" />
      <div class="header-right"  v-if="query.readonly !== '1'">
          <a-button type="primary" html-type="submit" 
               @click="onFinish">保存</a-button>
        </div>
    </div>
    <div class="content">
      <a-form
        :model="formState"
        name="basic"
        ref="formRef"
        autocomplete="off"
        @finish="onFinish"
      >
        <a-card title="基本信息" :bordered="false">
          <a-row :gutter="32">
            <a-col :span="12">
              <a-form-item
                label="电站名称"
                name="stationName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false }]"
              >
                <a-input v-model:value="formState.stationName" disabled />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="期数"
                name="periodId"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '期数不能为空。' }]"
              >
                <a-select
                  v-model:value="formState.periodId"
                  allowClear
                  :options="periodList"
                  :disabled="disabledInput"
                  @change="changeSelectPeriod"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="缺陷类型"
                name="faultType"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '缺陷类型不能为空。' }]"
              >
                <DictSelectBox
                  :type="`fault_type`"
                  :disabled="disabledInput"
                  v-model:value="formState.faultType"
                  style="width: 100%"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="来源任务"
                name="sourceBillCode"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false }]"
              >
                <!-- <a-input
                  v-model:value="formState.sourceBillCode"
                  disabled
                  style="color: blue; text-decoration: underline; cursor: pointer"
                /> -->
                <a @click="forwardPage">{{ formState.sourceBillCode }}</a>
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="发现时间"
                name="findDate"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '发现时间' }]"
              >
                <a-date-picker
                  v-model:value="formState.findDate"
                  valueFormat="YYYY-MM-DD HH:mm"
                  format="YYYY-MM-DD HH:mm"
                  style="width: 100%"
                  :disabled="disabledInput"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="发现人"
                name="findById"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '发现人' }]"
              >
                <a-select
                  v-model:value="formState.findById"
                  allowClear
                  :options="findList"
                  :disabled="disabledInput"
                  @change="
                    (id, item) => {
                      formState.findById = id;
                      formState.findByName = item.label;
                    }
                  "
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="物料类别"
                name="materialName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false, message: '物料类别' }]"
              >
                <a-input v-model:value="formState.materialName" row="3" disabled />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="设备名称"
                name="equipmentName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '设备名称' }]"
              >
                <SearchBox
                  v-model:searchText="equipmentName"
                  v-model:value="formState.equipmentName"
                  :columns="searchBoxColumns"
                  :disabled="disabledInput"
                  :api="getEquipmentUrl"
                  placeholder="请先选择期数"
                  :apiParamFunc="getParamFunc"
                  :pagination="true"
                  style="width: 100%"
                  vfield="name"
                  @change="setSelectData"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="设备编码"
                name="equipmentCode"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false, message: '设备编码' }]"
              >
                <a-input v-model:value="formState.equipmentCode" row="3" disabled />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="供应商"
                name="supplierName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false, message: '供应商' }]"
              >
                <a-input v-model:value="formState.supplierName" row="3" disabled />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="生产厂家"
                name="factoryName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false, message: '生产厂家' }]"
              >
                <a-input v-model:value="formState.factoryName" row="3" disabled />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="区域"
                name="areaName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false, message: '区域' }]"
              >
                <a-input v-model:value="formState.areaName" row="3" disabled />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="投运日期"
                name="usedDate"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false, message: '投运日期' }]"
              >
                <a-input v-model:value="formState.usedDate" row="3" disabled />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="出厂编码"
                name="productionNumber"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false, message: '出厂编码' }]"
              >
                <a-input v-model:value="formState.productionNumber" row="3" disabled />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="质保期内"
                name="isWarrantyPeriod"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '质保期内不能为空' }]"
              >
                <a-select
                  v-model:value="formState.isWarrantyPeriod"
                  :disabled="disabledInput"
                  allow-clear
                >
                  <a-select-option value="1">是</a-select-option>
                  <a-select-option value="0">否</a-select-option>
                </a-select>
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="缺陷等级"
                name="faultGrade"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '缺陷等级' }]"
              >
                <DictSelectBox
                  :type="`fault_grade`"
                  :disabled="disabledInput"
                  v-model:value="formState.faultGrade"
                  style="width: 100%"
                />
              </a-form-item>
            </a-col>

            <a-col :span="24">
              <a-form-item
                label="检查内容"
                name="checkContent"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '检查内容不能为空' }]"
              >
                <a-textarea
                  v-model:value="formState.checkContent"
                  row="3"
                  :disabled="disabledInput"
                  maxlength="512"
                />
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item
                label="原因分析"
                name="reason"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '原因分析不能为空' }]"
              >
                <a-textarea
                  v-model:value="formState.reason"
                  row="2"
                  :disabled="disabledInput"
                  maxlength="512"
                />
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item
                label="计划整改措施"
                name="checkContent"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '计划整改措施不能为空' }]"
              >
                <a-textarea
                  v-model:value="formState.measure"
                  row="3"
                  :disabled="disabledInput"
                  maxlength="512"
                />
              </a-form-item>
            </a-col>
          </a-row>
        </a-card>
        <a-card title="附件上传" :bordered="false">
          <UploadBox
            :width="800"
            :height="550"
            :maxCount="20"
            :maxSize="100 * 1024 * 1024"
            :application="`po`"
            :module="`inventory`"
            :vmode="!disabledInput ? `box` : `view`"
            :bizId="formState.bizFileId"
          />
        </a-card>
      </a-form>
    </div>
  </div>
</a-spin>
</div>
</PageWrapper>
</template>

<script lang="ts" setup>
  //form
  import { PageWrapper } from '/@/components/Page';
  import { reactive, ref, onMounted,toRaw,computed } from 'vue';
  import { urlToPath } from '/@/utils/route';
  import { FormInstance } from 'ant-design-vue';
  // import PrintButton from '/@/components/Framework/Button/PrintButton.vue';
  import {
    addFaultDangerData,
    getFaultDangerData,
    getStationPeriodData,
    getFaultPeriodData,
    getStationPerson,
    getEquipmentUrl,
  } from './create';
  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';
  import BillTitle from '/@/components/Framework/BillTitle/BillTitle.vue';
  import dayjs from 'dayjs';
  import {  addTabPage } from '@/utils/route';
  import SearchBox from '@/components/Framework/Combox/SearchBox.vue';
  import UploadBox from '@/components/Framework/Combox/UploadBox.vue';
  import { buildShortUUID } from '@/utils/uuid';
  import { SysMessage } from '/@/hooks/web/useMessage';
  import { MsgManager } from '/@/message/MsgManager';
  import { useUserStore } from '/@/store/modules/user';


  const userStore = useUserStore();
  const userInfo = toRaw(userStore.getUserInfo);

  const disabledInput=computed(()=>{
    return query.readonly==='1' || (formState.workBillId!=null && formState.workBillId!='') 
        || (formState.sourceId!=null && formState.sourceId!='')
        || (formState.personMemberId !='' && formState.personMemberId.substring(0,32) != userInfo.userId);
  })

  const formRef = ref<FormInstance>();
  const periodList: any = ref([]);
  const findList: any = ref([]);
  const equipmentName = ref('');
  const searchBoxColumns: any = ref([
    { title: '设备名称', dataIndex: 'name', width: 120 },
    { title: '物料', dataIndex: 'materialName', width: 120 },
    { title: '生产厂家', dataIndex: 'factoryName', width: 120 },
    { title: '区域', dataIndex: 'areaName', width: 120 },
  ]);
  let {params:query} = urlToPath() as any;
  const formState:any = reactive({
    id: query.id || '',
    status: 0,
    stationOrganId: query.stationOrganId || '',
    stationOrganName: query.stationOrganName || '',
    stationId: query.stationId || '',
    stationName: query.stationName || '',
    periodId: '',
    periodName: '',
    sourceParentId: '',
    sourceId: '',
    sourceType: '',
    sourceBillCode: '',
    billCode: '',
    fillinDate: '',
    personMemberId: '',
    faultType: '',
    findDate: '',
    findById: '',
    findByName: '',
    isWarrantyPeriod: '',
    faultGrade: '',
    checkContent: '',
    spinning:false,
    reason: '',
    measure: '',
    equipmentId: '',
    equipmentName: '',
    materialName: '',
    equipmentCode: '',
    supplierName: '',
    factoryName: '',
    areaName: '',
    usedDate: '',
    productionNumber: '',
    bizFileId: '',
  });

  const getParamFunc = () => {
    if (disabledInput.value) {
      return false;
    }
    if (!formState.periodId) {
      return false;
    }

    return { periodId: formState.periodId,status:1 };
  };
  const forwardPage = () => {
    if (!formState.sourceId || !formState.sourceParentId) {
      return;
    }
    switch (formState.sourceType) {
      case 'technicalSupervision':
        // router.push({ path: `/po/supervision/create`, query: { id: formState.sourceParentId } });
        addTabPage(`/po/supervision/create`, '', { id: formState.sourceParentId });
        break;
    }
  };
  const onFinish = () => {
    const value = formRef.value;
    if (!value) {
      return;
    }
    formState.spinning=true;
    value
      .validateFields()
      .then(() => {
        addFaultDangerData(formState)
          .then((rsp) => {
            if (rsp.result != '') {
              formState.id = rsp.result;
              SysMessage.getInstance().success(formState.status == 1 ? '提交成功' : '保存成功');
              setTimeout(() => {
                MsgManager.getInstance().sendMsg('po-fault-manage', {});
              }, 100);
            } else {
              
              SysMessage.getInstance().error('保存失败.');
            }
            formState.spinning = false;
          })
          .catch((e) => {
            formState.spinning = false;
            console.log(e);
          });
      })
      .catch((e) => {
        console.log(e);
        formState.spinning = false;
      });
  };
  const billTitleOptions = reactive<any>({subject : '缺陷管理'});


  const changeSelectPeriod = (id, item) => {
    formState.periodId = id;
    formState.periodName = item.label;
    formState.faultType = '';
    formState.findDate = '';
    formState.findById = '';
    formState.findByName = '';
    formState.isWarrantyPeriod = '';
    formState.faultGrade = '';
    formState.checkContent = '';
    formState.reason = '';
    formState.measure = '';
    formState.equipmentId = '';
    formState.equipmentName = '';
    formState.materialName = '';
    formState.equipmentCode = '';
    formState.supplierName = '';
    formState.factoryName = '';
    formState.areaName = '';
    formState.usedDate = '';
    formState.productionNumber = '';
  };

  const setSelectData = (value, selectRow) => {
    let row = selectRow.record;
    formState.equipmentId = row.key;
    formState.equipmentName = row.name;
    formState.materialName = row.materialWholeName;
    formState.equipmentCode = row.code;
    formState.supplierName = row.supplierName;
    formState.factoryName = row.factoryName;
    formState.areaName = row.areaName;
    formState.usedDate = row.usedDate;
    formState.productionNumber = row.productionNumber;
    formState.isWarrantyPeriod = row.warrantyDate >= dayjs().format('YYYY-MM-DD') ? '1' : '0';
  };

  // global
  onMounted(async () => {
    getFaultDangerData({
      id: formState.id,
    })
      .then((rsp) => {
        let rs = rsp.result;
        if (formState.id) {
          for (let key in rs) {
            formState[key] = rs[key];
          }
          formState.isWarrantyPeriod = formState.isWarrantyPeriod + '';
          if (query.status) {
            formState.status = Number(query.status);
          }
          formState.status = Number(formState.status);
        } else {
          formState.billCode = rs.billCode;
          formState.personMemberName = rs.personMemberName;
          formState.deptName=rs.deptName;
          formState.fillinDate = rs.fillinDate;
          formState.bizFileId = buildShortUUID();
        }
        
        let { billCode, fillinDate, personMemberName,deptName } = rs;
        Object.assign(billTitleOptions, { billCode, fillinDate, personMemberName,deptName });
      })
      .then(() => {
        if (formState.sourceType != '') {
          //试验检测生成的缺陷单
          if (formState.sourceType == 'technicalSupervision') {
            getFaultPeriodData({ technicalSupervisionId: formState.sourceId }).then((rsp) => {
              rsp.result.forEach((e) => {
                periodList.value.push({ value: e.periodId, label: e.periodName });
              });
            });
          }
          findList.value.push({ value: formState.findById, label: formState.findByName });
        } else {
          //手动新增缺陷单
          getStationPeriodData({ powerStationId: formState.stationId }).then((rsp) => {
            rsp.result.forEach((e) => {
              periodList.value.push({
                value: e.powerStationPeriodId,
                label: e.powerStationPeriodName,
              });
            });
          });
          getStationPerson({ stationId: formState.stationId }).then((rsp) => {
            let tmp={};
            rsp.result.forEach((e) => {
              tmp[e.userId]=e.name;
            });
            for(let k in tmp){
              findList.value.push({
                label: tmp[k],
                value: k,
              });
            }
          });
        }
      });
  });
</script>
